"use client";
import React,{useState} from "react";
import {createUserWithEmailAndPassword} from "firebase/auth";
import auth from "@firebase/firebase";
export default function DangKy() {
    return (
        <div>
            <div className="container mx-auto md:w-1/2 border border-black p-4 mt-4">
                <h1 className="text-3xl text-red-500 font-bold text-center mb-4">Đăng ký</h1>

                <form>
                    <div className="grid grid-cols-2 gap-4">
                        <div className="text-sm font-medium text-gray-700">
                            <label htmlFor="name">Họ và tên</label>
                            <input type="text" id="name" className="border border-black p-2 w-full" placeholder="Nhập họ và tên" required/>
                        </div>

                        <div className="text-sm font-medium text-gray-700">
                            <label htmlFor="phone">Số điện thoại</label>
                            <input type="text" id="phone" className="border border-black p-2 w-full" placeholder="Nhập số điện thoại" required />
                        </div>

                        <div className="text-sm font-medium text-gray-700">
                            <label htmlFor="email">Email</label>
                            <input type="email" id="email" className="border border-black p-2 w-full" placeholder="Nhập email" required />
                        </div>

                        <div className="text-sm font-medium text-gray-700">
                            <label htmlFor="password">Mật khẩu</label>
                            <input type="password" id="password" className="border border-black p-2 w-full" placeholder="Nhập mật khẩu" required/>
                        </div>
                    </div>
                    <div className="grid grid-cols-2 gap-4 mt-4">
                        <button
                            type="submit"
                            className="bg-red-500 hover:bg-red-300 text-white font-bold py-2 px-4 rounded w-full">
                            Quay Trở Lại Đăng Nhập
                        </button>
                        <button
                            type="reset"
                            className="bg-red-500 hover:bg-red-200 text-white font-bold py-2 px-4 rounded w-full">
                            Đăng Ký
                        </button>
                    </div>
                 
                </form>
            </div>

        </div>
    );
}